<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="产品编号">
          <el-input v-model="listQuery.finished_product_code"></el-input>
        </el-form-item>

        <!-- <el-form-item label="生产计划">
          <el-select v-model="listQuery.product_plan_code" placeholder="请选择">
            <el-option
              v-for="(item, index) in ProductPlanCodeNameList"
              :key="index + ' search'"
              :label="item.product_plan_name"
              :value="item.product_plan_code"
            ></el-option>
          </el-select>
        </el-form-item> -->

        <el-form-item label="物料编号">
          <el-input v-model="listQuery.matter_id"></el-input>
        </el-form-item>

        <el-form-item label="经过工站">
          <!-- <el-input v-model="listQuery.work_id" ></el-input> -->
          <el-select v-model="listQuery.work_id" placeholder="请选择">
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' search'"
              :label="item.work_name"
              :value="item.work_id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="过站人员">
          <el-select
            v-model="listQuery.user_code"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in PersonCodeNameList"
              :key="index + ' dialog'"
              :label="item.user_name"
              :value="item.user_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出站时间">
          <el-date-picker
            v-model="listQuery.out_time"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="out_time_change"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>过站查询</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline"> </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="物料名称" prop="matter_name">
                <el-input v-model="dialogForm.matter_name" readonly></el-input>
              </el-form-item>

              <el-form-item label="规格型号" prop="rule">
                <el-input v-model="dialogForm.rule" readonly></el-input>
              </el-form-item>
              <el-form-item label="入库时间" prop="product_time">
                <el-input v-model="dialogForm.product_time" readonly></el-input>
              </el-form-item>
              <el-form-item label="过站人员" prop="user_code">
                <el-input v-model="dialogForm.user_code" readonly></el-input>
              </el-form-item>
              <el-form-item label="产品编号" prop="finished_product_code">
                <el-input
                  v-model="dialogForm.finished_product_code"
                  readonly
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="物料编号" prop="matter_id">
                <el-input v-model="dialogForm.matter_id" readonly></el-input>
              </el-form-item>
              <el-form-item label="类型" prop="matter_category">
                <el-input
                  v-model="dialogForm.matter_category"
                  readonly
                ></el-input>
              </el-form-item>
              <el-form-item label="经过工站" prop="work_name">
                <el-input v-model="dialogForm.work_name" readonly></el-input>
              </el-form-item>

              <el-form-item label="出站时间" prop="out_time">
                <el-input v-model="dialogForm.out_time" readonly></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <p class="dialog-title">产品明细</p>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="产品名称" prop="product_obj.product_name">
                <el-input
                  v-model="dialogForm.product_obj.product_name"
                  readonly
                ></el-input>
              </el-form-item>

              <el-form-item label="规格型号" prop="product_obj.rule">
                <el-input
                  v-model="dialogForm.product_obj.rule"
                  readonly
                ></el-input>
              </el-form-item>

              <el-form-item label="状态" prop="product_obj.status">
                <el-input
                  v-model="dialogForm.product_obj.status"
                  readonly
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label="产品编号"
                prop="product_obj.finished_product_code"
              >
                <el-input
                  v-model="dialogForm.product_obj.finished_product_code"
                  readonly
                ></el-input>
              </el-form-item>
              <el-form-item label="包装编码" prop="product_obj.pack_id">
                <el-input
                  v-model="dialogForm.product_obj.pack_id"
                  readonly
                ></el-input>
              </el-form-item>
              <el-form-item label="时间" prop="product_obj.enter_time">
                <el-input
                  v-model="dialogForm.product_obj.enter_time"
                  readonly
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MatterProductSearch",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        finished_product_code: "",
        matter_id: "",
        work_id: "",
        user_code: "",
        out_time: [],
        out_start_time: "",
        out_end_time: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        finished_product_code: "",
        matter_id: "",
        work_id: "",
        user_code: "",
        out_time: [],
        out_start_time: "",
        out_end_time: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 表格列
      tableCol: [
        {
          label: "物料名称",
          field: "matter_name",
          align: "center",
        },
        {
          label: "物料编号",
          field: "matter_id",
          align: "center",
        },
        {
          label: "规格型号",
          field: "rule",
          align: "center",
        },
        {
          label: "类型",
          field: "matter_category",
          align: "center",
        },
        {
          label: "入库时间",
          field: "product_time",
          align: "center",
        },
        {
          label: "经过工站",
          field: "work_name",
          align: "center",
        },
        {
          label: "过站人员",
          field: "user_name",
          align: "center",
        },
        {
          label: "出站时间",
          field: "out_time",
          align: "center",
        },
        {
          label: "产品编号",
          field: "finished_product_code",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "详情",
              event: this.colDetailBtn,
            },
          ],
        },
      ],
      tableData: [],
      NoPageProductData: [],
      dialogFlag: false,
      dialogForm: {
        matter_name: "",
        matter_id: "",
        rule: "",
        matter_category: "",
        product_time: "",
        work_name: "",
        user_code: "",
        out_time: "",
        finished_product_code: "",
        product_obj: {
          prodect_code: "",
          finished_product_code: "",
          status: "",
          rule: "",
          pack_id: "",
          enter_time: "",
        },
      },
      dialogFormBase: {
        matter_name: "",
        matter_id: "",
        rule: "",
        matter_category: "",
        product_time: "",
        work_name: "",
        user_code: "",
        out_time: "",
        finished_product_code: "",
        product_obj: {
          prodect_code: "",
          finished_product_code: "",
          status: "",
          rule: "",
          pack_id: "",
          enter_time: "",
        },
      },
    };
  },

  computed: {
    ...mapState(["PersonCodeNameList", "WorkCodeNameList"]),
  },
  methods: {
    ...mapActions(["getLogisticPersonCodeName", "getLogisticWorkCodeName"]),

    out_time_change() {
        console.log("this.listQuery.out_time",this.listQuery.out_time);
        if(this.listQuery.out_time.length>0){
          this.listQuery.out_start_time=this.listQuery.out_time[0]
          this.listQuery.out_end_time=this.listQuery.out_time[1]
        }

    },
    // 通过物料 查询产品编号
    getLogisticNoPageProductInfoSearch() {
      baseManagement.getLogisticNoPageProductInfoSearch().then((res) => {
        console.log("通过物料 查询产品编号", res);
        if (res.code == 0) {
          this.NoPageProductData = res.data.data;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticMatterSearch(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticMatterSearch(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticMatterSearch(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticMatterSearch(this.listQuery);
    },
    // 过站查询 查询
    async getLogisticMatterSearch(params) {
      // 人员内码和人员名称 查询
      await this.getLogisticPersonCodeName();
      baseManagement.getLogisticMatterSearch(params).then((res) => {
        console.log("过站查询 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.user_code == CodeNameListItem.user_code) {
                tableDataItem.user_name = CodeNameListItem.user_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    colDetailBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "物料详情";
      this.dialogFlag = true;

      this.dialogForm = {
        matter_name: rowData.matter_name,
        matter_id: rowData.matter_id,
        rule: rowData.rule,
        matter_category: rowData.matter_category,
        product_time: rowData.product_time,
        work_name: rowData.work_name,
        user_code: rowData.user_code,
        out_time: rowData.out_time,
        finished_product_code: rowData.finished_product_code,
        response_datas: rowData.response_datas,
      };
      this.NoPageProductData.forEach((NoPageProductDataItem) => {
        if (
          rowData.finished_product_code ==
          NoPageProductDataItem.finished_product_code
        ) {
          this.dialogForm.product_obj = JSON.parse(
            JSON.stringify(NoPageProductDataItem)
          );
        }
      });
      console.log("dialogTbaleData", dialogTbaleData);
    },
  },
  mounted() {
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 过站查询 查询
    this.getLogisticMatterSearch(this.listQuery);
    // 通过物料 查询产品编号
    this.getLogisticNoPageProductInfoSearch();
  },
};
</script>

